.baseline {
    display: flex;
    align-items: baseline;
    justify-content: center;
}

.baseline > div {
    font-size: 6rem;
}

.mainPage {
    color: antiquewhite;
    width: 60%;
    height: 700px;
}

.update-time {
    font-size: 1.6rem;
}

.divider {
    background-color: antiquewhite;
}

@media (max-width: 500px){
    .mainPage {
        width: 100%;
        height: 80%;
    }
}